/* ==============/
    Add Fonts
/============== */

@font-face {
    font-family: 'Shabnam';
    font-style: normal;
    src: url('fonts/Vazir.eot');
    /* IE9 Compat Modes */
    src: local('Vazir'), local('Vazir'), url('fonts/Vazir.eot') format('embedded-opentype'), /* IE6-IE8 */
    url('fonts/Vazir.woff2') format('woff2'), /* Super Modern Browsers */
    url('fonts/Vazir.woff') format('woff'), /* Modern Browsers */
    url('fonts/Vazir.ttf') format('truetype')/* Safari, Android, iOS */
}

@font-face {
    font-family: 'Vazir';
    font-style: normal;
    font-weight: bold;
    src: url('fonts/Vazir-Bold.eot');
    /* IE9 Compat Modes */
    src: local('Vazir-Bold'), local('Vazir-Bold'), url('fonts/Vazir-Bold.eot') format('embedded-opentype'), /* IE6-IE8 */
    url('fonts/Vazir-Bold.woff2') format('woff2'), /* Super Modern Browsers */
    url('fonts/Vazir-Bold.woff') format('woff'), /* Modern Browsers */
    url('fonts/Vazir-Bold.ttf') format('truetype')/* Safari, Android, iOS */
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
    line-height: 1;
    background-color: white !important;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

a{
    text-decoration: none !important;
}

